<template>
  <div id="root">
    <div class="main">
      <div class="content" style="height: 100%; margin-top: 50px">
        <div style="width: 100%; margin-top: 0px">
          <div style="flex-direction: column">
            <div class="jzEppV" v-if="currentPage === 0">
              <img src="/dolcetice/img/logo.jpeg" style="width: 200px" />
              <br />
              <label style="color: rgb(142, 77, 45)">쿠폰북</label>
              <div style="padding: 20px 20px">
                <b-form-input
                  type="number"
                  placeholder="핸드폰 번호를 입력하세요."
                  v-model="tel"
                  size="lg"
                ></b-form-input>
                <b-button variant="success" @click="go">확인</b-button>
              </div>
            </div>
            <div v-if="currentPage === 1">
              <label style="color: rgb(142, 77, 45); margin-bottom: 10px">{{
                `${user}님 ${freeTicket}장 사용가능합니다.`
              }}</label>
              <div>
                <b-avatar
                  button
                  icon="star-fill"
                  size="4em"
                  :variant="d < coupon ? 'primary' : 'secondary'"
                  v-for="(d, idx) in allCoupon1"
                  :key="'allCoupon' + idx"
                  @click="couponClick(d)"
                  v-b-modal.modal-1
                ></b-avatar>
              </div>
              <div>
                <b-avatar
                  button
                  icon="star-fill"
                  size="4em"
                  :variant="d < coupon ? 'primary' : 'secondary'"
                  v-for="(d, idx) in allCoupon2"
                  :key="'allCoupon2' + idx"
                  @click="couponClick(d)"
                ></b-avatar>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Main",
  data() {
    return {
      tel: "01041886073",
      currentPage: 0,
      coupon: 0,
      allCoupon1: [0, 1, 2, 3, 4],
      allCoupon2: [5, 6, 7, 8, 9],
      freeTicket: 0,
      user: "",
    };
  },
  mounted() {},
  methods: {
    couponClick(d) {
      let text = "추가";
      if (d < this.$data.coupon) {
        text = "삭제";
      }
      this.$bvModal
        .msgBoxConfirm(`쿠폰 ${text} 하시겠습니까?`)
        .then((value) => {
          if (text === "추가") {
            if (value) {
              if (this.$data.coupon < 9) {
                this.$data.coupon++;
              } else {
                this.$data.coupon = 0;
                this.$data.freeTicket++;
              }
            }
          } else {
            if (value) {
              if (this.$data.coupon > 0) {
                this.$data.coupon--;
              }
            }
          }
        });
    },
    toast(msg, title = "") {
      this.$bvToast.toast(msg, {
        title,
        toaster: "b-toaster-top-center",
        solid: true,
      });
    },
    go() {
      const tel = this.$data.tel;
      const user = [{ tel: "01041886073", name: "한영완", coupon: 3 }];
      const filter = user.filter((f) => {
        return f.tel === tel;
      });

      if (filter.length === 0) {
        this.toast("등록되지 않은 회원입니다.");
        return;
      } else {
        //this.toast(filter[0].name);
        this.$data.currentPage = 1;
        this.$data.coupon = filter[0].coupon % 10;
        this.$data.freeTicket = parseInt(filter[0].coupon / 10);
        this.$data.user = filter[0].name;
      }
    },
  },
};
</script>

<style scoped>
.content,
.main {
  width: 100%;
  height: auto;
  max-width: 570px;
  min-width: 320px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: center;
  justify-content: center;
}
.main {
  -webkit-flex-direction: column;
  flex-direction: column;
  text-align: center;
  background: #f7f4e3;
}
.jzEppV {
  font-size: 40px;
  margin-bottom: 120px;
}
</style>
